@import url(https://fonts.googleapis.com/css?family=Maven+Pro&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons&display=swap);
.indexListItem{padding-bottom:5px}.linkWithoutColor,.docsLink,.selectedDocsLink,.subDocsLink,.selectedSubDocsLink{color:inherit}.docsLink,.selectedDocsLink{font-size:20px}.selectedDocsLink{font-weight:bold;padding-left:15px}.subDocsLink,.selectedSubDocsLink{font-size:16px;padding-left:25px}.selectedSubDocsLink{font-weight:bold;padding-left:30px}

.contentsTitle{color:#3d98ff}.code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;background-color:black}.highlightedTerm{color:#3d98ff;font-weight:bold}.linkToOtherDocs{color:#3d98ff}

.docsBody{color:rgba(255,255,255,0.774)}.docsTitle{margin-top:0}.docsContent{display:flex}.column,.centerColumn{flex:1 1;padding:20px;background-color:#3a3939;min-height:600px}.centerColumn{flex:2 1;background-color:#1e1e1e;border-left:1px solid white}

.App{display:flex;justify-content:space-between;padding-top:20px}.header .welcome{color:rgba(255,255,255,0.685);display:inline}.helpAndBackButton,.helpButton,.backToStub4Button{background-color:#4494df;border:none;color:black;padding:15px 0;font-size:14px;text-align:center;font-weight:bold;border-radius:2px;float:right;min-width:155px}.material-icons{font-family:'Material Icons';font-weight:normal;font-style:normal;font-size:16px;display:inline-block;line-height:16px;text-transform:none;letter-spacing:normal;word-wrap:normal;white-space:nowrap;direction:ltr;vertical-align:middle;-webkit-font-smoothing:antialiased;text-rendering:optimizeLegibility;-moz-osx-font-smoothing:grayscale;font-feature-settings:'liga';padding-right:5px}html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit;font-family:'Maven Pro', sans-serif}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;padding:30px;background-color:#1e1e1e}

